:root {
  --color-primary: #eb2f64;
  --color-primary-light: #ff3366;
  --color-primary-dark: #ba265d;
  --color-secondary: #faa300;
  --color-secondary-light: #ffbf00;
  --color-secondary-dark: #4b2801;
  --color-grey-light-1: #faf9f9;
  --color-grey-light-2: #f4f2f2;
  --color-grey-light-3: #f0eeee;
  --color-grey-light-4: #ccc;
  --color-grey-dark-1: #222;
  --color-grey-dark-2: #333;
  --color-grey-dark-3: #777;
  --color-grey-dark-4: #999;
  --color-black: #111;
  --shadow-light: 0 2rem 5rem rgba(0, 0, 0, 0.06);
  --shadow-dark: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  --line: 1px solid var(--color-grey-light-2);
  --line: 1px solid var(--color-grey-dark-2);
  --default-font: 2.5rem;
  --header-height: 32.5rem;
}
* {
  margin: 0;
  padding: 0;
}
html {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 62.5%;
}
*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
}
body {
  min-height: 100vh;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  line-height: normal;
  color: #faf9f9;
  color: var(--color-grey-light-1);
  background: -o-radial-gradient(
    #faf9f9,
    #ffbf00,
    #faa300,
    #4b2801);
  background: -o-radial-gradient(
    var(--color-grey-light-1),
    var(--color-secondary-light),
    var(--color-secondary),
    var(--color-secondary-dark)
  );
  background: radial-gradient(
    #faf9f9,
    #ffbf00,
    #faa300,
    #4b2801);
  background: radial-gradient(
    var(--color-grey-light-1),
    var(--color-secondary-light),
    var(--color-secondary),
    var(--color-secondary-dark)
  );
  background-attachment: fixed;
}
::-moz-selection {
  color: #faf9f9;
  color: var(--color-grey-light-1);
  background-color: #eb2f64;
  background-color: var(--color-primary);
}
::selection {
  color: #faf9f9;
  color: var(--color-grey-light-1);
  background-color: #eb2f64;
  background-color: var(--color-primary);
}
.heading-primary {
  font-family: 'Bowlby One SC', cursive;
  letter-spacing: 1.5rem;
  color: #222;
  color: var(--color-grey-dark-1);
  text-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  text-shadow: var(--shadow-dark);
  text-align: center;
  text-shadow: -1.5px -1.5px 0 #faf9f9,
    1.5px -1.5px 0 #faf9f9,
    -1.5px 1.5px 0 #faf9f9,
    1.5px 1.5px 0 #faf9f9;
  text-shadow: -1.5px -1.5px 0 var(--color-grey-light-1),
    1.5px -1.5px 0 var(--color-grey-light-1),
    -1.5px 1.5px 0 var(--color-grey-light-1),
    1.5px 1.5px 0 var(--color-grey-light-1);
}
.heading-primary--main {
  font-size: 10rem;
  display: block;
  text-transform: uppercase;
}
.heading-primary--sub {
  font-size: 4rem;
}
.heading-secondary {
  font-family: 'Overlock SC', cursive;
  color: #f0eeee;
  color: var(--color-grey-light-3);
  font-size: 5rem;
  text-shadow: -1.5px -1.5px 0 #222,
    1.5px -1.5px 0 #222,
    -1.5px 1.5px 0 #222,
    1.5px 1.5px 0 #222;
  text-shadow: -1.5px -1.5px 0 var(--color-grey-dark-1),
    1.5px -1.5px 0 var(--color-grey-dark-1),
    -1.5px 1.5px 0 var(--color-grey-dark-1),
    1.5px 1.5px 0 var(--color-grey-dark-1);
  text-align: center;
}
.paragraph {
  font-size: 2.5rem;
  font-size: var(--default-font);
  text-align: center;
  line-height: 3.75rem;
}
.paragraph--lined {
  border-top: 1px solid #333;
  border-top: var(--line);
  border-bottom: 1px solid #333;
  border-bottom: var(--line);
  padding-top: 1rem;
  padding-bottom: 1rem;
}
.header {
  height: 32.5rem;
  height: var(--header-height);
  width: 100%;
  position: relative;
  z-index: -1;
}
.content {
  background-color: #222;
  background-color: var(--color-grey-dark-1);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  padding: 4rem 25% 5rem 25%;
  border-top: 1px solid #333;
  border-top: var(--line);
}
.footer {
  background-color: #111;
  background-color: var(--color-black);
  height: 6rem;
  -webkit-box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: var(--shadow-dark);
  box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  box-shadow: var(--shadow-dark);
}
.navigation {
  background-color: #111;
  background-color: var(--color-black);
  height: 8rem;
  -webkit-box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: var(--shadow-dark);
  box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  box-shadow: var(--shadow-dark);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: stretch;
  align-items: stretch;
  -ms-flex-pack: justify;
  justify-content: space-between;
}
.navigation__nav-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  -ms-flex-pack: center;
  justify-content: center;
  width: 33.3333333333%;
  text-align: center;
  font-family: 'Overlock SC', cursive;
  font-size: 4rem;
}
.title__logo-box {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-image: -o-radial-gradient(
    #ff3366,
    #ba265d);
  background-image: -o-radial-gradient(
    var(--color-primary-light),
    var(--color-primary-dark)
  );
  background-image: radial-gradient(
    #ff3366,
    #ba265d);
  background-image: radial-gradient(
    var(--color-primary-light),
    var(--color-primary-dark)
  );
  height: calc(32.5rem + (0.133333333333 * 32.5rem));
  height: calc(var(--header-height) + (0.133333333333 * var(--header-height)));
  width: calc(32.5rem + (0.133333333333 * 32.5rem));
  width: calc(var(--header-height) + (0.133333333333 * var(--header-height)));
  border-radius: 50%;
  -webkit-box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  -webkit-box-shadow: var(--shadow-dark);
  box-shadow: 0 2rem 6rem rgba(0, 0, 0, 0.3);
  box-shadow: var(--shadow-dark);
}
.title__text-box {
  width: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  background-color: transparent;
}

